
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
#signUpAndLogIn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 50px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 400px;
  height: 280px;
  border: 1px solid black;
  background: #333;
  color: #fff;
  border-radius: 5px;
}
#signUpAndLogIn > .tips {
    font-size: 14px;
}
#signUpAndLogIn > .tips > .title {
      position: absolute;
      right: 3em;
      top: 2em;
}
#signUpAndLogIn > .tips > .title > h1 {
        line-height: 1;
}
#signUpAndLogIn > .tips > span {
      position: relative;
      padding: 0 10px;
}
#signUpAndLogIn > .tips > span > i {
        position: absolute;
        color: #85d2bb;
        left: -.1em;
        top: .1em;
}
#signUpAndLogIn > .tips > span.active {
      color: #85d2bb;
}
#signUpAndLogIn > .signupForm > .username,
  #signUpAndLogIn > .loginForm > .username {
    margin: 16px 0;
}
#signUpAndLogIn > .signupForm > .password,
  #signUpAndLogIn > .loginForm > .password {
    margin: 16px 0;
}
#signUpAndLogIn > .signupForm > .action input,
  #signUpAndLogIn > .loginForm > .action input {
    background: #85d2bb;
    cursor: pointer;
    outline: none;
    border: none;
    color: #fff;
    font-weight: bold;
    width: 80px;
    height: 35px;
    border-radius: 5px;
}



